<!-- @/components/PetCakeForm.vue -->
<template>
  <el-form
    ref="formRef"
    :model="form"
    :rules="rules"
    label-width="100px"
    class="pet-cake-form"
  >
    <!--  -->
    <div class="form-section">
      <div class="section-title"></div>
      <el-form-item label="" prop="name" required>
        <el-input v-model="form.name" placeholder="" />
      </el-form-item>
      <el-form-item label="" prop="description" required>
        <el-input
          v-model="form.description"
          type="textarea"
          :rows="4"
          placeholder=""
        />
      </el-form-item>
    </div>

    <!--  -->
    <div class="form-section">
      <div class="section-title"></div>
      <div class="table-container">
        <el-table :data="form.specs" border style="width: 100%">
          <el-table-column label="" width="200">
            <template #default="{ row, $index }">
              <el-form-item :prop="`specs.${$index}.number`" :rules="requiredRule">
                <el-input v-model="row.number" placeholder="" />
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column label="()">
            <template #default="{ row, $index }">
              <el-form-item :prop="`specs.${$index}.price`" :rules="requiredRule">
                <el-input-number v-model="row.price" :min="0" :precision="2" style="width:100%" />
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column label="(g)">
            <template #default="{ row, $index }">
              <el-form-item :prop="`specs.${$index}.weight`" :rules="weightRule">
                <el-input-number v-model="row.weight" :min="100" :step="50" style="width:100%" />
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column label="" width="100" align="center">
            <template #default="{ $index }">
              <el-button
                v-if="form.specs.length > 1"
                type="danger"
                size="small"
                icon="Delete"
                circle
                @click="removeSpec($index)"
              />
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <!--  -->
    <div class="form-section">
      <div class="section-title"></div>
      <!--   prop="image"  label -->
      <el-form-item label="" prop="image">
        <el-upload
          action="/api/common/upload"
          :headers="uploadHeaders"
          :data="{ type: 'pet_cake' }"
          :on-success="handleUploadSuccess"
          :before-upload="beforeUpload"
          :show-file-list="false"
          accept="image/*"
        >
          <el-button type="primary"></el-button>
        </el-upload>
        <div v-if="form.image" class="image-preview">
          <img :src="form.image" alt="" style="height: 100px; margin-top: 10px" />
          <el-button size="small" type="danger" @click="form.image = null"></el-button>
        </div>
      </el-form-item>
    </div>

    <!--  -->
    <el-form-item>
      <el-button type="primary" @click="submitForm"></el-button>
      <el-button @click="emit('cancel')"></el-button>
    </el-form-item>
  </el-form>
</template>

<script setup>
import { ref, watch, computed } from 'vue'
import { Delete } from '@element-plus/icons-vue'
import { useUserStore } from '@/store/user'
import { ElMessage } from 'element-plus'

const props = defineProps({
  isEdit: Boolean,
  initialData: Object
})
const emit = defineEmits(['submit', 'cancel'])

const userStore = useUserStore()
const formRef = ref(null)

// 
const form = ref({
  name: '',
  image: '',
  description: '',
  specs: [
    { number: '', price: 98, weight: 300 }
  ]
})

//  
const requiredRule = { required: true, message: '', trigger: 'blur' }
const weightRule = { required: true, message: '100g', trigger: 'blur' }

//  
const rules = {
  name: [
    { required: true, message: '', trigger: 'blur' },
    { min: 1, max: 50, message: ' 1  50 ', trigger: 'blur' }
  ],
  description: [
    { required: true, message: '', trigger: 'blur' }
  ],
  image: [
    { required: true, message: '', trigger: 'change' }
  ]
  // specs  requiredRule 
}

// 
const uploadHeaders = computed(() => ({
  token: userStore.token
}))

const beforeUpload = (file) => {
  if (file.size / 1024 / 1024 > 2) {
    ElMessage.error('2MB')
    return false
  }
  return true
}

const handleUploadSuccess = (res) => {
  if (res.code === 200) {
    form.value.image = res.data
    ElMessage.success('')
  }
}

const removeSpec = (index) => {
  if (form.value.specs.length > 1) {
    form.value.specs.splice(index, 1)
  }
}

//   validate 
const submitForm = () => {
  formRef.value.validate((valid) => {
    if (valid) {
      emit('submit', { ...form.value })
    } else {
      ElMessage.error('')
    }
  })
}

// 
watch(
  () => props.initialData,
  (val) => {
    if (val) {
      form.value = { ...val }
      //  specs 
      if (!form.value.specs || form.value.specs.length === 0) {
        form.value.specs = [{ number: '', price: 98, weight: 300 }]
      }
    }
  },
  { immediate: true, deep: true }
)

// 
const setFormData = (data) => {
  form.value = { ...data }
}
defineExpose({ setFormData })
</script>

<style scoped>
.section-title {
  font-weight: 600;
  margin-bottom: 15px;
  color: #8B6B3D;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(214, 187, 112, 0.3);
}
.pet-cake-form .form-section {
  background: #fefcf6;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
  border: 1px solid rgba(214, 187, 112, 0.3);
}
.table-container {
  width: 100%;
  overflow-x: auto;
}
.table-container :deep(.el-table) {
  min-width: 800px;
  width: 100% !important;
  border: 1px solid rgba(214, 187, 112, 0.3);
  border-radius: 6px;
}
.table-container :deep(.el-table th) {
  background: linear-gradient(to bottom, #f8f4e9, #f1ead5);
  color: #8B6B3D;
  font-weight: 600;
  border-bottom: 1px solid rgba(214, 187, 112, 0.3);
}
.table-container :deep(.el-table td) {
  white-space: nowrap;
  min-width: 120px;
  border-bottom: 1px solid rgba(214, 187, 112, 0.1);
}
.table-container :deep(.el-table__row:hover td) {
  background-color: #fefaf0;
}
.image-preview {
  margin-top: 10px;
}

/*  */
:deep(.el-button--primary) {
  background: linear-gradient(45deg, #d6bb70, #c4a85f);
  border: none;
  border-radius: 6px;
  transition: all 0.3s;
}
:deep(.el-button--primary:hover) {
  background: linear-gradient(45deg, #c4a85f, #b3984e);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.4);
}

/*  */
:deep(.el-input__wrapper) {
  border-radius: 6px;
  transition: all 0.3s;
}
:deep(.el-input__wrapper:hover) {
  border-color: #d6bb70;
  box-shadow: 0 0 0 1px rgba(214, 187, 112, 0.3);
}
:deep(.el-input__wrapper.is-focus) {
  border-color: #d6bb70;
  box-shadow: 0 0 0 1px rgba(214, 187, 112, 0.5);
}
:deep(.el-textarea__inner) {
  border-radius: 6px;
  transition: all 0.3s;
}
:deep(.el-textarea__inner:hover) {
  border-color: #d6bb70;
}
:deep(.el-textarea__inner:focus) {
  border-color: #d6bb70;
  box-shadow: 0 0 0 1px rgba(214, 187, 112, 0.3);
}

/*  */
:deep(.el-button:not(.el-button--primary)) {
  color: #8B6B3D;
  border: 1px solid rgba(214, 187, 112, 0.5);
  border-radius: 6px;
  transition: all 0.3s;
}
:deep(.el-button:not(.el-button--primary):hover) {
  background: rgba(214, 187, 112, 0.1);
  color: #745c2d;
  border-color: #d6bb70;
  transform: translateY(-2px);
}

/*  */
:deep(.el-upload .el-button--primary) {
  background: linear-gradient(45deg, #d6bb70, #c4a85f);
  border: none;
}
:deep(.el-upload .el-button--primary:hover) {
  background: linear-gradient(45deg, #c4a85f, #b3984e);
}

/*  */
:deep(.el-button--danger) {
  background: #f56c6c;
  border: none;
}
:deep(.el-button--danger:hover) {
  background: #f78989;
  transform: scale(1.05);
}
</style>