import request from "@/utils/request";

//
export function createOrder(data){
    return request({
        url: "/order/create",
        method: "put",
        data: data
    })
}

//
export function getOrderList(params){
    return request({
        url: "/order/list",
        method: "get",
        params: params

    })
}

//

export function payOrder(data){
    return request({
        url: '/order/pay',
        method: "post",
        data:data
    })
}

//
export function cancelOrder(data){
    return request({
        url: "/order/cancel",
        method: "post",
        data: data
    })
}