import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '@/views/HomeView.vue'
import { useUserStore } from '@/store/user'
import DessertView from '@/views/DessertView.vue'
import PetCakeView from '@/views/PetCakeView.vue'
import ShoppingCart from '@/views/ShoppingCart.vue'
import SearchView from '@/views/SearchView.vue'
import CheckoutView from '@/views/CheckoutView.vue'
import OrderView from '@/views/OrderView.vue'
import PersonalCenter from '@/views/PersonalCenter.vue'
import CooperationZone from '@/views/CooperationZone.vue'
import AboutUs from '@/views/AboutUs.vue'
import PaymentPage from '@/views/PaymentPage.vue'
import SimplePayment from '@/views/SimplePayment.vue'


const routes = [
  {
    path: '/home',
    name: 'home',
    component: HomeView,
    meta: { title: '' }
  },
    {
    path: '/cakes',
    name: 'Cakes',
    component: () => import('@/views/CakeView.vue'),
    meta: { title: '' }
    },
  {
    path: '/desserts',
    name: 'desserts',
    component: DessertView,
    meta: { title: '' }
  },
  {
    path: '/pet',
    name: 'pet',
    component: PetCakeView,
    meta: { title: '' }
  },
  {
    path: '/cake/:id',  // 
    name: 'CakeDetail',
    component: () => import('@/views/CakeDetail.vue'),
    props: true  // props
  },
  {
    path: '/',
    redirect: '/login'  // 
  },
   {
        path: '/login',
        name: 'Login',
        component: () => import("@/views/Login/LoginView.vue")
    },
     {
        path: '/register',
        name: 'Register',
        component: () => import("@/views/register/RegisterView.vue")
    },
    {
      path: '/cart',
      name: 'Cart',
      component: ShoppingCart,
      meta:{title:''}
    },
    {
    path: '/search',
    name: 'Search',
    component: SearchView,
    // 
    meta: { title: '' }
  },
  {
    path: '/checkout',
    name: 'Name',
    component: CheckoutView,
    meta: {title: ''}
  },
  {
    path: '/order',
    name: 'order',
    component: OrderView,
    meta: {title: ""}
  },
  {
    path: '/person',
    name: 'person',
    component: PersonalCenter,
    meta: {title: ""}
  },
  {
    path: '/cooperation',
    name: 'cooperation',
    component: CooperationZone,
    meta: {title: ""}
  },
  {
    path: '/us',
    name: 'us',
    component: AboutUs,
    meta: {title: ""}
  },
   {
    path: '/pay',
    name: 'pay',
    component:PaymentPage,
    meta: {title: ""}
  },
  {
    path:'/simplePay',
    name:'simplePay',
    component:SimplePayment,
    meta:{title:""}
  }
]

const router = createRouter({
//   history: createWebHistory(import.meta.env.BASE_URL ),
  history: createWebHistory('/'),
  routes,
  scrollBehavior(to, from, savedPosition) {
    return { top: 0 }
  }
})

// 
router.beforeEach((to, from, next) => {
  document.title = to.meta.title || ''
  next()
})


// 
function inWhiteList(toPath) {
    const whiteList = ['/login', '/register', '/404']
    const path = whiteList.find((value) => {
        // 
        const reg = new RegExp('^' + value)
        return reg.test(toPath)
    })
    return !!path
}

/* router.beforeEach((to, from, next) => {
    const token = sessionStorage.getItem("token")

    if (inWhiteList(to.path)) {
        next()
    } else {
        //
        if (token) {
            next()
        } else {
            next(`/login`)
        }
    }
}) */

    router.beforeEach((to, from, next) => {
  const userStore = useUserStore()
  
  if (to.meta.requiresAuth && !userStore.token) {
    next('/login')
  } else if (to.meta.requiresGuest && userStore.token) {
    next('/')
  } else {
    next()
  }
})


export default router