<template>
  <div class="order-list-container">
    <div class="page-header">
      <h2></h2>
      <p class="page-subtitle"></p>
    </div>

    <!--  -->
    <div class="filter-section">
      <el-radio-group v-model="statusFilter" @change="loadOrders">
        <el-radio-button :label="null"></el-radio-button>
        <el-radio-button :label="0"></el-radio-button>
        <el-radio-button :label="1"></el-radio-button>
        <el-radio-button :label="2"></el-radio-button>
      </el-radio-group>
    </div>

    <!--  -->
    <div v-if="orders.length > 0" class="order-list">
      <el-card 
        shadow="hover" 
        v-for="order in orders" 
        :key="order.id" 
        class="order-card"
        :class="{'cancelled-order': order.payStatus === 2}"
      >
        <!--  -->
        <div class="order-header">
          <div class="order-info">
            <div class="order-no">
              <el-icon><Document /></el-icon>
              {{ order.orderNo }}
            </div>
            <el-tag class="status-tag" :type="statusTagType(order.payStatus)">
              {{ statusText(order.payStatus) }}
            </el-tag>
          </div>
          <div class="order-time">
            <el-icon><Clock /></el-icon>
            {{ formatDate(order.orderTime) }}
          </div>
        </div>


        <!--  -->
        <div v-if="order.payStatus === 0 && order.createTime" class="countdown-section">
          <div class="countdown-info">
            <el-icon><Clock /></el-icon>
            <span class="countdown-text"></span>
            <span class="countdown-timer" :class="getCountdownClass(order.remainingTime)">
              {{ formatCountdown(order.remainingTime) }}
            </span>
          </div>
          <div class="countdown-tip"></div>
        </div>

        <!--  -->
        <div class="address-section" v-if="order.address || order.consignee">
          <div class="address-header">
            <el-icon><Location /></el-icon>
            <span></span>
          </div>
          <div class="address-content">
            <div class="address-line">
              <span class="address-label"></span>
              <span class="address-value">{{ order.consignee || '' }}</span>
            </div>
            <div class="address-line">
              <span class="address-label"></span>
              <span class="address-value">{{ order.phone || '' }}</span>
            </div>
            <div class="address-line">
              <span class="address-label"></span>
              <span class="address-value">{{ order.address || '' }}</span>
            </div>
          </div>
        </div>

        <!--  -->
        <div class="products-section">
          <div class="section-title"></div>
          <div class="product-list">
            <div class="product-item" v-for="item in order.orderDetails" :key="item.id">
              <img :src="item.image" class="product-image" />
              <div class="product-info">
                <div class="product-name">{{ item.name }}</div>
                <div class="product-specs">
                  <span class="spec-item">{{ item.number || '' }}</span>
                  <span class="spec-item">{{ item.num }}</span>
                  <span class="spec-item">{{ item.amount.toFixed(2) }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!--  -->
        <div class="order-summary">
          <div class="order-remark" v-if="order.remark">
            <el-icon><ChatDotRound /></el-icon>
            {{ order.remark }}
          </div>
          <div class="order-total">
            <span class="total-amount">{{ order.totalAmount.toFixed(2) }}</span>
          </div>
        </div>

        <!-- "" -->
        <div v-if="order.payStatus === 2" class="cancel-info">
          <div class="cancel-time">
            <el-icon><Clock /></el-icon>
            {{ formatDate(order.cancelTime) }}
          </div>
          <div class="cancel-reason" v-if="order.cancelReason">
            <el-icon><Warning /></el-icon>
            {{ order.cancelReason }}
          </div>
        </div>
        
        <!--  -->
        <div class="order-actions">
          <el-button 
            class="action-btn detail-btn"
            @click="viewOrderDetail(order)"
          >
            <el-icon><View /></el-icon>
            
          </el-button>
          
          <!--  -->
          <template v-if="order.payStatus === 0">
            <el-button
              class="action-btn cancel-btn"
              @click="cancelOrder(order)"
            >
              <el-icon><Close /></el-icon>
              
            </el-button>
            <el-button
              class="action-btn pay-btn"
              @click="payOrder(order)"
            >
              <el-icon><Money /></el-icon>
              
            </el-button>
          </template>
          
          <!--  -->
          <template v-if="order.payStatus === 1">
            <el-button
              class="action-btn secondary"
              @click="contactService(order)"
            >
              <el-icon><Service /></el-icon>
              
            </el-button>
            <el-button
              class="action-btn secondary"
              @click="requestRefund(order)"
            >
              <el-icon><RefreshRight /></el-icon>
              
            </el-button>
          </template>
        </div>
      </el-card>
    </div>

    <!--  -->
    <el-empty v-else description="" :image-size="200">
      <el-button type="primary" @click="router.push('/home')"  class="shopping-btn"></el-button>
    </el-empty>

    <!--  -->
    <el-dialog
      v-model="showDetailModal"
      :title="` - ${selectedOrder?.orderNo || ''}`"
      width="700px"
      class="order-detail-dialog"
    >
      <div v-if="selectedOrder" class="order-detail">
        <div class="detail-section">
          <h3></h3>
          <el-descriptions :column="2" border>
            <el-descriptions-item label="">{{ selectedOrder.orderNo }}</el-descriptions-item>
            <el-descriptions-item label="">{{ formatDate(selectedOrder.orderTime) }}</el-descriptions-item>
            <el-descriptions-item label="">
              <el-tag :type="statusTagType(selectedOrder.payStatus)">{{ statusText(selectedOrder.payStatus) }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="">
              {{ selectedOrder.payMethod === 1 ? '' : '' }}
            </el-descriptions-item>
          </el-descriptions>
        </div>

        <div class="detail-section">
          <h3></h3>
          <el-descriptions :column="1" border>
            <el-descriptions-item label="">{{ selectedOrder.consignee || '' }}</el-descriptions-item>
            <el-descriptions-item label="">{{ selectedOrder.phone || '' }}</el-descriptions-item>
            <el-descriptions-item label="">{{ selectedOrder.address || '' }}</el-descriptions-item>
          </el-descriptions>
        </div>

        <div class="detail-section">
          <h3></h3>
          <el-table :data="selectedOrder.orderDetails" size="small" class="detail-table">
            <el-table-column prop="name" label="" width="200" />
            <el-table-column label="" width="80">
              <template #default="scope">
                <img :src="scope.row.image" class="table-product-image" />
              </template>
            </el-table-column>
            <el-table-column prop="number" label="" width="120" />
            <el-table-column prop="num" label="" width="80" />
            <el-table-column label="" width="100">
              <template #default="scope">
                {{ scope.row.amount.toFixed(2) }}
              </template>
            </el-table-column>
            <el-table-column label="" width="100">
              <template #default="scope">
                {{ (scope.row.amount * scope.row.num).toFixed(2) }}
              </template>
            </el-table-column>
          </el-table>
        </div>

        <div class="detail-total">
          <div class="total-line">
            <span class="total-label"></span>
            <span class="total-value">{{ selectedOrder.totalAmount.toFixed(2) }}</span>
          </div>
        </div>

        <div class="detail-remark" v-if="selectedOrder.remark">
          <el-icon><ChatDotRound /></el-icon>
          {{ selectedOrder.remark }}
        </div>
      </div>
    </el-dialog>

    <!--  order-list-container  -->
    <el-dialog
      v-model="showCancelDialog"
      title=""
      width="500px"
    >
      <div class="cancel-dialog-content">
        <p class="cancel-tip"></p>
        
        <el-select
          v-model="cancelForm.cancelReason"
          placeholder=""
          class="cancel-reason-select"
          size="large"
          @change="handleCancelReasonChange"
        >
          <el-option
            v-for="reason in cancelReasons"
            :key="reason.value"
            :label="reason.label"
            :value="reason.value"
          />
        </el-select>
        
        <el-input
          v-if="cancelForm.cancelReason === 'other'"
          v-model="cancelForm.otherReason"
          type="textarea"
          placeholder=""
          :rows="3"
          maxlength="200"
          show-word-limit
          style="margin-top: 16px;"
        />
      </div>
      
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="showCancelDialog = false" :disabled="canceling">
            
          </el-button>
          <el-button 
            type="primary" 
            @click="confirmCancelOrder" 
            :loading="canceling"
            :disabled="!cancelForm.cancelReason || (cancelForm.cancelReason === 'other' && !cancelForm.otherReason)"
          >
            
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, onMounted ,onUnmounted,reactive } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { 
  Document, 
  Clock, 
  Location, 
  ChatDotRound, 
  Warning, 
  View, 
  Close, 
  Money,
  Service,
  RefreshRight
} from '@element-plus/icons-vue'
import { getOrderList, cancelOrder as apiCancelOrder, payOrder as apiPayOrder} from '@/api/order'
import { useRouter } from 'vue-router'

const router = useRouter()



// 
const showDetailModal = ref(false)
const selectedOrder = ref(null)


// 
const statusFilter = ref(null)

// 
const orders = ref([])

// 
let countdownTimer = null

// 
const loadOrders = async () => {
  const params = {}
  
  if (statusFilter.value === 0 || statusFilter.value === 1 || statusFilter.value === 2) {
    params.payStatus = statusFilter.value
  }

  try {
    const res = await getOrderList(params)
    orders.value = res.data || []
    
    //  map 
    orders.value = orders.value.map(order => {
      if (order.payStatus === 0 && order.createTime) {
        const remainingTime = calculateRemainingTime(order.createTime)
        return {
          ...order,
          remainingTime,
          needRefresh: remainingTime <= 0
        }
      }
      return order
    })
    
    // 
    startCountdownTimer()
  } catch (error) {
    ElMessage.error('')
    console.error(':', error)
  }
}

// 30
const calculateRemainingTime = (createTime) => {
  if (!createTime) return 0

  let createDate
 //  [, , , , , ]
  if (Array.isArray(createTime) && createTime.length >= 6) {
    //  0  1
    createDate = new Date(
      createTime[0], // 
      createTime[1] - 1, //  (0-11)
      createTime[2], // 
      createTime[3], // 
      createTime[4], // 
      createTime[5]  // 
    )
  } else {
    // 
    createDate = new Date(createTime)
  }
  const now = new Date()
  const timeDiff = now - createDate  // 
  
  // 30 = 30 * 60 * 1000 = 1800000 
  const totalTime = 30 * 60 * 1000
  const remainingTime = Math.max(0, totalTime - timeDiff)
  
  return Math.floor(remainingTime / 1000)  // 
}

// 
const updateOrderRemainingTime = (order) => {
  if (order.payStatus !== 0) return
  
  const remainingSeconds = calculateRemainingTime(order.createTime)
  order.remainingTime = remainingSeconds
  
  // 
  if (remainingSeconds <= 0) {
    order.needRefresh = true
  }
}

// 
const formatCountdown = (seconds) => {
  if (seconds <= 0) return '00:00:00'
  
  const hours = Math.floor(seconds / 3600)
  const minutes = Math.floor((seconds % 3600) / 60)
  const secs = seconds % 60
  
  return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
}

// 
const getCountdownClass = (seconds) => {
  if (seconds <= 300) { // 5
    return 'countdown-urgent'
  } else if (seconds <= 600) { // 10
    return 'countdown-warning'
  }
  return 'countdown-normal'
}

// 
// 
const startCountdownTimer = () => {
  if (countdownTimer) {
    clearInterval(countdownTimer)
  }
  
  countdownTimer = setInterval(() => {
    let needRefresh = false
    
    orders.value.forEach(order => {
      if (order.payStatus === 0 && order.remainingTime !== undefined) {
        // 
        order.remainingTime = Math.max(0, order.remainingTime - 1)
        
        // 0
        if (order.remainingTime <= 0 && !order.needRefresh) {
          order.needRefresh = true
          needRefresh = true
        }
      }
    })
    
    // 
    if (needRefresh) {
      loadOrders()
    }
  }, 1000)
}

// 
const stopCountdownTimer = () => {
  if (countdownTimer) {
    clearInterval(countdownTimer)
    countdownTimer = null
  }
}

// 
const viewOrderDetail = (order) => {
  selectedOrder.value = order
  showDetailModal.value = true
}

// 
const showCancelDialog = ref(false)
const canceling = ref(false)
const selectedOrderForCancel = ref(null)
const cancelForm = reactive({
  cancelReason: '',
  otherReason: ''
})

const cancelReasons = [
  { value: 'change_mind', label: '' },
  { value: 'wrong_info', label: '' },
  { value: 'found_cheaper', label: '' },
  { value: 'out_of_stock', label: '' },
  { value: 'delivery_issue', label: '' },
  { value: 'payment_issue', label: '' },
  { value: 'other', label: '' }
]

// 
const cancelOrder = (order) => {
  selectedOrderForCancel.value = order
  // 
  cancelForm.cancelReason = ''
  cancelForm.otherReason = ''
  showCancelDialog.value = true
}

// 
const handleCancelReasonChange = (value) => {
  if (value !== 'other') {
    cancelForm.otherReason = ''
  }
}

// 
const confirmCancelOrder = async () => {
  if (!selectedOrderForCancel.value) return
  
  if (!cancelForm.cancelReason) {
    ElMessage.error('')
    return
  }

  if (cancelForm.cancelReason === 'other' && !cancelForm.otherReason.trim()) {
    ElMessage.error('')
    return
  }

  canceling.value = true
  try {
    const cancelData = {
      id: selectedOrderForCancel.value.id,
      cancelReason: cancelForm.cancelReason === 'other' ? 
                   cancelForm.otherReason.trim() : 
                   cancelReasons.find(r => r.value === cancelForm.cancelReason)?.label
    }

    await apiCancelOrder(cancelData)
    ElMessage.success('')
    showCancelDialog.value = false
    loadOrders()
  } catch (error) {
    ElMessage.error('')
  } finally {
    canceling.value = false
  }
}

// 
const payOrder = (order) => {
  

   

    router.push({
      path:"/simplePay",
      query:{
        id: order.id,
        amount:order.totalAmount,
        orderNo: order.orderNo

      }
    })

    
    loadOrders()
  }


// 
const contactService = (order) => {
  ElMessage.info(` ${order.orderNo}`)
}

// 
const requestRefund = (order) => {
  ElMessageBox.confirm(` ${order.orderNo} `, '', {
    type: 'warning',
    confirmButtonText: '',
    cancelButtonText: ''
  }).then(() => {
    ElMessage.success('24')
  }).catch(() => {
    ElMessage.info('')
  })
}

// 
const statusText = (status) => {
  switch (status) {
    case 0: return ''
    case 1: return ''
    case 2: return ''
    default: return ''
  }
}

// Tag 
const statusTagType = (status) => {
  switch (status) {
    case 0: return 'warning'
    case 1: return 'success'
    case 2: return 'info'
    default: return ''
  }
}

const formatDate = (input) => {
  let date

  if (!input) return ''

  if (Array.isArray(input)) {
    const [year, month, day, hour = 0, minute = 0, second = 0] = input
    date = new Date(year, month - 1, day, hour, minute, second)
  } else {
    date = new Date(input)
  }

  if (isNaN(date.getTime())) return ''

  const pad = (n) => n.toString().padStart(2, '0')
  return `${date.getFullYear()}-${pad(date.getMonth() + 1)}-${pad(date.getDate())} ${pad(date.getHours())}:${pad(date.getMinutes())}:${pad(date.getSeconds())}`
}

onMounted(() => {
  loadOrders()
})

onUnmounted(() => {
  stopCountdownTimer()
})
</script>

