<template>
  <div class="cart">
    <el-card shadow="never" :body-style="{ padding: '30px' }">
      <h2></h2>

      <!--  -->
      <el-table 
        :data="cartItems" 
        style="width: 100%" 
        v-if="cartItems.length > 0" 
        :show-header="false"
        :row-style="{ height: '100px' }"
      >
        <!--  -->
        <el-table-column width="60" align="center">
          <template #default="scope">
            <el-checkbox v-model="scope.row.checked" @change="updateTotal"></el-checkbox>
          </template>
        </el-table-column>

        <!--  -->
        <el-table-column prop="name" label="">
          <template #default="scope">
            <div class="product-info" @click="goToProductDetail(scope.row.productId)">
              <img :src="scope.row.image" class="cart-image" />
              <div class="product-details">
                <div class="product-name">{{ scope.row.name }}</div>
                <div class="product-number">{{ scope.row.number }}</div>
              </div>
            </div>
          </template>
        </el-table-column>

        <!--  -->
        <el-table-column label="" width="200" align="center">
          <template #default="scope">
            <div class="price-comparison">
              <template v-if="scope.row.nowAmount != null && scope.row.amount != null">
                <div v-if="scope.row.nowAmount < scope.row.amount" class="price-down">
                    {{ (scope.row.amount - scope.row.nowAmount).toFixed(2) }}
                </div>
                <div v-else-if="scope.row.nowAmount > scope.row.amount" class="price-up">
                    {{ (scope.row.nowAmount - scope.row.amount).toFixed(2) }}
                </div>
                <div v-else>
                  
                </div>
                <div class="original-price">
                   {{ scope.row.amount }}   {{ scope.row.nowAmount }}
                </div>
              </template>
              <template v-else>
                <!-- nowAmountamount -->
                ...
              </template>
            </div>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="" width="140" align="center">
          <template #default="scope">
            <span class="price-highlight">{{ scope.row.amount }}</span>
          </template>
        </el-table-column>

        <!--  -->
        <el-table-column label="" width="160" align="center">
          <template #default="scope">
            <el-input-number
              size="small"
              v-model="scope.row.num"
              :min="1"
              @change="updateItem(scope.row)"
            ></el-input-number>
          </template>
        </el-table-column>

        <!--  -->
        <el-table-column label="" width="120" align="center">
          <template #default="scope">
            <el-button type="text" @click="removeItem(scope.row.id)" class="delete-btn"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <div v-else class="empty-cart">
        <el-empty description="" :image-size="80">
          <template #default>
            <el-button 
              type="primary" 
              class="go-home-btn"
              @click="goHome"
            >
              
            </el-button>
          </template>
        </el-empty>
      </div>


      <!--  -->
      <div class="cart-footer" v-if="cartItems.length > 0">
        <div class="footer-left">
          <el-checkbox v-model="allChecked" @change="toggleAll"></el-checkbox>
           <span class="batch-delete-btn" style="margin-left: 24px;" @click="batchDelete"></span>
           <span class="clear-all-btn" @click="clearAllCart"></span>
        </div>
        <div class="footer-right">
          <div class="total-price">
             <span>{{ checkedCount }}</span> 
          </div>
          <div class="total-price">
            <span class="price-highlight">{{ totalPrice }}</span>
          </div>
          <el-button type="primary" size="large" @click="checkout" class="checkout-btn"></el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue'
import { ElMessage,ElMessageBox} from 'element-plus'
import { useCartStore } from '@/store/cart'
import { useRouter } from 'vue-router'
import {updateCart,removeCart,batchRemoveCart,clearCart} from '@/api/shoppingCart'

const router = useRouter()
const cartStore = useCartStore()
const cartItems = computed(() => cartStore.items)

// 
const totalPrice = computed(() => {
  return cartItems.value
    .filter(item => item.checked)
    .reduce((sum, item) => sum + item.amount * item.num, 0)
    .toFixed(2)
})

// 
const checkedCount = computed(() => {
  return cartItems.value.filter(item => item.checked).length
})

const goHome = () => {
  router.push('/home')
}

// 
const allChecked = computed({
  get: () => cartItems.value.length > 0 && cartItems.value.every(item => item.checked),
  set: (value) => {
    cartItems.value.forEach(item => {
      item.checked = value
    })
    updateTotal()
  }
})

// 
onMounted(async () => {
  try {
    await cartStore.loadCart()
    // 
    cartItems.value.forEach(item => {
      if (item.checked === undefined) {
        item.checked = true
      }
    })
  } catch (error) {
    ElMessage.error('')
  }
})

// 
const updateItem = async (item) => {
  try {
    await updateCart(item)
    
  } catch (error) {
    ElMessage.error('')
  }
}

// 
const removeItem = async (id) => {
  try {
    await removeCart(id)
    ElMessage.success('')
    cartStore.loadCart()
  } catch (error) {
    ElMessage.error('')
  }
}

// 
const batchDelete = async () => {
  const checkedItems = cartItems.value.filter(item => item.checked)
  if (checkedItems.length === 0) {
    ElMessage.warning('')
    return
  }

  const ids = checkedItems.map(item => item.id) //  id

  ElMessageBox.confirm(
    ` ${ids.length} `,
    '',
    {
      type: 'warning',
      confirmButtonText: '',
      cancelButtonText: ''
    }
  )
    .then(async () => {
      try {
        await batchRemoveCart(ids) // 
        await cartStore.loadCart()   // 
        ElMessage.success('')
        updateTotal()
      } catch (error) {
        ElMessage.error('')
      }
    })
    .catch(() => {
      ElMessage.info('')
    })
}

// 
const clearAllCart = async () => {

   // 
  if (cartItems.value.length === 0) {
    ElMessage.info('')
    return
  }
  try {

    // 
    await ElMessageBox.confirm(
      '',
      '',
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }
    )
    
    await clearCart()
    ElMessage.success('')
    cartStore.loadCart();
  } catch (error) {
    ElMessage.error('')
  }
}

// 
const checkout = () => {
  if (checkedCount.value === 0) {
    ElMessage.warning('')
    return
  }

  //
  const selectedItems = cartItems.value.filter(item => item.checked)

  //
   router.push({
    path: '/checkout',
    query: {items: JSON.stringify(selectedItems)}
  })

}

// 
const updateTotal = () => {
  // store
}

// /
const toggleAll = (value) => {
  cartItems.value.forEach(item => {
    item.checked = value
  })
  updateTotal()
}

// 
const goToProductDetail = (productId) => {
  router.push(`/cake/${productId}`)
}
</script>

<style scoped>
.cart {
  max-width: 1200px;
  margin: 20px auto;
  padding: 0 20px;
  box-sizing: border-box;
}

/*  */
h2 {
  color: #d6bb70;
  margin-bottom: 24px;
  text-align: center;
  font-weight: 600;
}

/*  */
.cart-image {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 8px;
  margin-right: 16px;
  cursor: pointer;
  transition: transform 0.2s;
  border: 1px solid #f5ecd8;
}

.cart-image:hover {
  transform: scale(1.05);
}

/*  */
.product-info {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 10px 0;
}

.product-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.product-name {
  font-weight: 600;
  font-size: 15px;
  color: #333;
  margin-bottom: 4px;
}

.product-number {
  font-size: 13px;
  color: #999;
}

/*  */
.price-comparison {
  font-size: 13px;
}

.price-down {
  color: #67c23a;
  font-weight: 500;
  margin-bottom: 4px;
}

.price-up {
  color: #f56c6c;
  font-weight: 500;
  margin-bottom: 4px;
}

.original-price {
  color: #999;
  font-size: 12px;
}

/*  */
.empty-cart {
  text-align: center;
  color: #999;
  padding: 60px 0;
}

/*  */
.cart-footer {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #f5ecd8;
  padding-top: 24px;
  background-color: #fefaf0;
  border-radius: 8px;
  padding: 20px;
}

.footer-left {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #666;
}

.batch-delete-btn {
  margin-left: 24px;
  color: #d6bb70;
  cursor: pointer;
  font-weight: 500;
  transition: color 0.3s;
}

.batch-delete-btn:hover {
  color: #c4a85f;
  text-decoration: underline;
}

.clear-all-btn {
  margin-left: 24px;
  color: #e6a23c;
  cursor: pointer;
  font-weight: 500;
  transition: color 0.3s;
}

.clear-all-btn:hover {
  color: #cf9236;
  text-decoration: underline;
}

.footer-right {
  display: flex;
  align-items: center;
  gap: 24px;
}

.total-price {
  font-size: 15px;
  color: #333;
}

.total-price span {
  color: #d6bb70;
  font-weight: bold;
  font-size: 16px;
}

.price-highlight {
  color: #d6bb70;
  font-weight: bold;
  font-size: 16px;
}

/*  */
.delete-btn {
  color: #d6bb70;
  transition: color 0.3s;
}

.delete-btn:hover {
  color: #c4a85f;
}

.go-home-btn {
  background-color: #d6bb70;
  border: none;
  color: white;
  font-weight: 500;
  padding: 10px 22px;
  border-radius: 8px;
  transition: all 0.3s;
}

.go-home-btn:hover {
  background-color: #c4a85f;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}


/*  */
.checkout-btn {
  background-color: #d6bb70;
  border: none;
  color: white;
  font-weight: 500;
  transition: all 0.3s;
  padding: 12px 24px;
}

.checkout-btn:hover {
  background-color: #c4a85f;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

/*  */
:deep(.el-checkbox__input.is-checked .el-checkbox__inner) {
  background-color: #d6bb70;
  border-color: #d6bb70;
}

:deep(.el-checkbox__input.is-checked + .el-checkbox__label) {
  color: #d6bb70;
}

/*  */
:deep(.el-input-number__increase),
:deep(.el-input-number__decrease) {
  background-color: #f5ecd8;
  color: #d6bb70;
  border-color: #f5ecd8;
}

:deep(.el-input-number__increase:hover),
:deep(.el-input-number__decrease:hover) {
  background-color: #d6bb70;
  color: white;
}

:deep(.el-input-number .el-input__inner) {
  border-color: #f5ecd8;
}

:deep(.el-input-number .el-input__inner:focus) {
  border-color: #d6bb70;
}

/*  */
:deep(.el-table) {
  background-color: #fffdf7;
}

:deep(.el-table th) {
  display: none;
}

:deep(.el-table .cell) {
  display: flex;
  align-items: center;
  justify-content: center;
  word-break: normal;
}

:deep(.el-table--enable-row-hover .el-table__body tr:hover>td) {
  background-color: #fefaf0 !important;
}

/*  */
:deep(.el-card) {
  background-color: #fffdf7;
  border: 1px solid #f5ecd8;
  border-radius: 12px;
}

/*  */
@media (max-width: 768px) {
  .cart {
    padding: 0 10px;
  }

  .cart-footer {
    flex-direction: column;
    gap: 12px;
    text-align: center;
  }

  .footer-right {
    justify-content: center;
    gap: 16px;
    flex-wrap: wrap;
  }

  .total-price {
    margin-right: 0;
  }
}
</style>