<template>
  <div class="payment-container">
    <!--  -->
    <div class="progress-steps">
      <div class="step completed">
        <div class="step-circle">1</div>
        <span class="step-text"></span>
      </div>
      <div class="step completed">
        <div class="step-circle">2</div>
        <span class="step-text"></span>
      </div>
      <div class="step active">
        <div class="step-circle">3</div>
        <span class="step-text"></span>
      </div>
    </div>

    <!--  -->
    <el-card shadow="never" class="payment-card">
      <div class="order-header">
        <el-icon class="success-icon"><CircleCheck /></el-icon>
        <div class="order-info">
          <h3></h3>
          <p>{{ orderInfo.orderNo }}</p>
          <p class="order-amount"><span class="amount">{{ orderInfo.totalAmount }}</span></p>
        </div>
      </div>

      <!--  -->
      <div class="countdown-section">
        <div class="countdown-title"></div>
        <div class="countdown-timer">
          <span class="time-unit">{{ countdown.hours }}</span>
          <span class="time-separator">  </span>
          <span class="time-unit">{{ countdown.minutes }}</span>
          <span class="time-separator">  </span>
          <span class="time-unit">{{ countdown.seconds }}</span>
        </div>
        <p class="countdown-tip"></p>
      </div>

      <!--  -->
      <div class="payment-methods">
        <h4></h4>
        <div class="payment-options">
          <div
            class="payment-method"
            :class="{ active: selectedPayment === 1 }"
            @click="selectedPayment = 1"
          >
            <div class="payment-icon wechat-icon"></div>
            <span></span>
          </div>
          <div
            class="payment-method"
            :class="{ active: selectedPayment === 2 }"
            @click="selectedPayment = 2"
          >
            <div class="payment-icon alipay-icon"></div>
            <span></span>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="password-section" v-if="showPasswordInput">
        <h4></h4>
        <div class="password-input-container">
          <el-input
            v-model="password"
            :type="showPassword ? 'text' : 'password'"
            placeholder=""
            :maxlength="20"
            @input="onPasswordInput"
            class="password-input"
            :disabled="paying"
            @keyup.enter="confirmPayment"
          >
            <template #suffix>
              <el-icon @click="togglePasswordVisibility" style="cursor: pointer;">
                <View v-if="!showPassword" />
                <Hide v-else />
              </el-icon>
            </template>
          </el-input>
          <div class="password-mask" v-if="!showPassword">
            <span
              v-for="i in password.length"
              :key="i"
              class="password-asterisk"
            >*</span>
          </div>
        </div>
        <p class="password-tip">6-20</p>
      </div>

      <!--  -->
      <div class="payment-actions">
        <el-button @click="showCancelDialog" class="cancel-btn" :loading="canceling">
          
        </el-button>
        <el-button 
          type="primary" 
          @click="handlePayment" 
          class="pay-btn"
          :loading="paying"
          :disabled="!canPay"
        >
          {{ showPasswordInput ? '' : '' }} {{ orderInfo.totalAmount }}
        </el-button>
      </div>

      <!--  -->
      <div class="order-tips">
        <p>20:00</p>
      
      </div>
    </el-card>

    <!--  -->
    <el-dialog
      v-model="cancelDialogVisible"
      title=""
      width="500px"
      :before-close="handleCancelDialogClose"
    >
      <div class="cancel-dialog-content">
        <p class="cancel-tip"></p>
        
        <el-form :model="cancelForm" :rules="cancelRules" ref="cancelFormRef">
          <el-form-item prop="cancelReason">
            <el-select
              v-model="cancelForm.cancelReason"
              placeholder=""
              class="cancel-reason-select"
              size="large"
            >
              <el-option
                v-for="reason in cancelReasons"
                :key="reason.value"
                :label="reason.label"
                :value="reason.value"
              />
            </el-select>
          </el-form-item>
          
          <el-form-item v-if="cancelForm.cancelReason === 'other'" prop="otherReason">
            <el-input
              v-model="cancelForm.otherReason"
              type="textarea"
              placeholder=""
              :rows="3"
              maxlength="200"
              show-word-limit
            />
          </el-form-item>
        </el-form>
      </div>
      
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="handleCancelDialogClose" :disabled="canceling">
            
          </el-button>
          <el-button 
            type="primary" 
            @click="confirmCancelOrder" 
            :loading="canceling"
            :disabled="!cancelForm.cancelReason"
          >
            
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>



<script setup>
import { ref, reactive, onMounted, onUnmounted, computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { ElMessage, ElMessageBox } from 'element-plus'
import { CircleCheck, View, Hide } from '@element-plus/icons-vue'
import { cancelOrder as cancelOrderApi, getOrderDetail, payOrder } from '@/api/order'

const route = useRoute()
const router = useRouter()

// 
const orderInfo = ref({
  id: '',
  orderNo: '',
  totalAmount: '0.00',
})

// 
const selectedPayment = ref(1)

// 
const password = ref('')
const showPasswordInput = ref(false)
const showPassword = ref(false)

// 
const countdown = reactive({
  hours: '00',
  minutes: '00',
  seconds: '00'
})

// 
const paying = ref(false)
const canceling = ref(false)

// 
const cancelDialogVisible = ref(false)
const cancelFormRef = ref()
const cancelForm = reactive({
  cancelReason: '',
  otherReason: ''
})

// 
const cancelReasons = [
  { value: 'change_mind', label: '' },
  { value: 'wrong_info', label: '' },
  { value: 'found_cheaper', label: '' },
  { value: 'out_of_stock', label: '' },
  { value: 'delivery_issue', label: '' },
  { value: 'payment_issue', label: '' },
  { value: 'timeout', label: '' },
  { value: 'other', label: '' }
]

// 
const cancelRules = {
  cancelReason: [
    { required: true, message: '', trigger: 'change' }
  ],
  otherReason: [
    { required: true, message: '', trigger: 'blur' }
  ]
}

// 
let countdownTimer = null
const totalSeconds = ref(30 * 60) // 30

// 
const canPay = computed(() => {
  return orderInfo.value.id && totalSeconds.value > 0
})

// 
const onPasswordInput = (value) => {
  password.value = value
}

// 
const togglePasswordVisibility = () => {
  showPassword.value = !showPassword.value
}

// 
const handlePayment = () => {
  if (!showPasswordInput.value) {
    // 
    showPasswordInput.value = true
    // 
    setTimeout(() => {
      const passwordInput = document.querySelector('.password-input input')
      if (passwordInput) {
        passwordInput.focus()
      }
    }, 100)
  } else {
    // 
    confirmPayment()
  }
}

// 
const showCancelDialog = () => {
  // 
  cancelForm.cancelReason = ''
  cancelForm.otherReason = ''
  cancelDialogVisible.value = true
}

// 
const handleCancelDialogClose = () => {
  if (!canceling.value) {
    cancelDialogVisible.value = false
  }
}

// 
const confirmCancelOrder = async () => {
  // 
  if (!cancelForm.cancelReason) {
    ElMessage.error('')
    return
  }

  if (cancelForm.cancelReason === 'other' && !cancelForm.otherReason) {
    ElMessage.error('')
    return
  }

  canceling.value = true
  try {
    //  -  cancelReason 
    const cancelData = {
      id: orderInfo.value.id,
      cancelReason: cancelForm.cancelReason === 'other' ? 
                   cancelForm.otherReason : 
                   cancelReasons.find(r => r.value === cancelForm.cancelReason)?.label
    }

    console.log(':', cancelData)
    
    // API
    await cancelOrderApi(cancelData)
    
    ElMessage.success('')
    cancelDialogVisible.value = false
    router.push('/order')
  } catch (error) {
    console.error(':', error)
    ElMessage.error('')
  } finally {
    canceling.value = false
  }
}

// 
const initCountdown = () => {
  countdownTimer = setInterval(() => {
    if (totalSeconds.value <= 0) {
      clearInterval(countdownTimer)
      handleTimeout()
      return
    }

    totalSeconds.value--
    updateCountdownDisplay()
  }, 1000)
}

// 
const updateCountdownDisplay = () => {
  const hours = Math.floor(totalSeconds.value / 3600)
  const minutes = Math.floor((totalSeconds.value % 3600) / 60)
  const seconds = totalSeconds.value % 60

  countdown.hours = hours.toString().padStart(2, '0')
  countdown.minutes = minutes.toString().padStart(2, '0')
  countdown.seconds = seconds.toString().padStart(2, '0')
}

// 
const handleTimeout = async () => {
  try {
    if (orderInfo.value.id) {
      //  cancelReason 
      const cancelData = {
        id: orderInfo.value.id,
        cancelReason: ''
      }
      await cancelOrderApi(cancelData)
    }
    ElMessage.warning('')
    router.push('/order')
  } catch (error) {
    console.error(':', error)
    router.push('/order')
  }
}

//  - API
const confirmPayment = async () => {
  if (!orderInfo.value.id) {
    ElMessage.error('')
    return
  }

  if (totalSeconds.value <= 0) {
    ElMessage.error('')
    return
  }

  if (!password.value) {
    ElMessage.error('')
    return
  }

  if (password.value.length < 6) {
    ElMessage.error('6')
    return
  }

  paying.value = true
  try {
    // API
    const paymentData = {
      id: orderInfo.value.id,
      payMethod: selectedPayment.value,
      totalAmount: orderInfo.value.totalAmount,
      password: password.value
    }

    console.log(':', paymentData)
    
    // API
    const res = await payOrder(paymentData)
    console.log("", res)
    
    if (res.code === 200) {
      ElMessage.success('')
      // 
      router.push('/order')
    } else {
      // 
      ElMessage.error(res.message || '')
      // 
      password.value = ''
      // 
      setTimeout(() => {
        const passwordInput = document.querySelector('.password-input input')
        if (passwordInput) {
          passwordInput.focus()
        }
      }, 100)
    }
  } catch (error) {
    console.error(':', error)
    ElMessage.error('')
    // 
    password.value = ''
  } finally {
    paying.value = false
  }
}

// 
const viewOrderDetail = () => {
  if (orderInfo.value.id) {
    router.push(`/order/detail/${orderInfo.value.id}`)
  } else {
    router.push('/order')
  }
}

// 
const continueShopping = () => {
  router.push('/')
}

// 
const loadOrderData = async () => {
  // 
  const { orderId, orderNo, amount } = route.query
  
  if (!orderId && !orderNo) {
    ElMessage.error('')
    router.back()
    return
  }
  
  // API
  orderInfo.value = {
    id: orderId || '',
    orderNo: orderNo || `ORDER${Date.now()}`,
    totalAmount: amount || '0.00',
  }
}

onMounted(() => {
  loadOrderData()
  updateCountdownDisplay()
  initCountdown()
})

onUnmounted(() => {
  if (countdownTimer) {
    clearInterval(countdownTimer)
  }
})
</script>

<style scoped>
/*  */
</style>

