  color: #d6bb70;
}

.countdown-tip {
  color: #999;
  font-size: 14px;
  margin: 0;
}

/*  */
.payment-methods {
  margin: 30px 0;
}

.payment-methods h4 {
  color: #333;
  margin-bottom: 16px;
  font-size: 16px;
}

.payment-options {
  display: flex;
  gap: 16px;
}

.payment-method {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 20px;
  border: 2px solid #f5ecd8;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s;
  background-color: white;
}

.payment-method:hover {
  border-color: #d6bb70;
}

.payment-method.active {
  border-color: #d6bb70;
  background-color: #fefaf0;
  box-shadow: 0 2px 8px rgba(214, 187, 112, 0.2);
}

.payment-icon {
  width: 32px;
  height: 32px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 14px;
}

.wechat-icon {
  background-color: #07C160;
}

.alipay-icon {
  background-color: #1677FF;
}

.payment-method span {
  font-size: 16px;
  color: #333;
  font-weight: 500;
}

/*  */
.password-section {
  margin: 30px 0;
  padding: 20px;
  background-color: #fefaf0;
  border-radius: 8px;
  border: 1px solid #f5ecd8;
}

.password-section h4 {
  color: #333;
  margin-bottom: 16px;
  text-align: center;
  font-size: 16px;
}

.password-input-container {
  position: relative;
  max-width: 300px;
  margin: 0 auto 12px;
}

.password-input :deep(.el-input__inner) {
  height: 50px;
  font-size: 16px;
  text-align: center;
  border: 2px solid #d6bb70;
  border-radius: 8px;
  background-color: white;
  color: transparent; /*  */
}

.password-input :deep(.el-input__inner:focus) {
  border-color: #c4a85f;
  box-shadow: 0 0 0 2px rgba(214, 187, 112, 0.2);
}

.password-mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 25px;
  pointer-events: none;
  gap: 8px;
}

.password-asterisk {
  font-size: 24px;
  color: #d6bb70;
  font-weight: bold;
}

.password-tip {
  text-align: center;
  color: #999;
  font-size: 14px;
  margin: 0;
}

/*  */
.payment-actions {
  display: flex;
  justify-content: center;
