<!-- src/components/AddressForm.vue -->
<template>
  <el-form :model="form" :rules="rules" ref="formRef" label-width="80px" :size="size">
    <el-form-item label="" prop="consignee">
      <el-input v-model="form.consignee" placeholder="" />
    </el-form-item>
    <el-form-item label="" prop="sex">
      <el-select v-model="form.sex" placeholder="">
        <el-option label="" value="" />
        <el-option label="" value="" />
      </el-select>
    </el-form-item>
    <el-form-item label="" prop="phone">
      <el-input v-model="form.phone" placeholder="" />
    </el-form-item>
    <el-form-item label="" prop="region">
      <el-cascader
        v-model="form.region"
        :options="regionOptions"
        placeholder="//"
        style="width: 100%;"
      />
    </el-form-item>
    <el-form-item label="" prop="detail">
      <el-input v-model="form.detail" type="textarea" :rows="2" placeholder="" />
    </el-form-item>
    <el-form-item label="" prop="isDefault">
      <el-switch
        v-model="form.isDefault"
        :active-value="1"
        :inactive-value="0"
      />
    </el-form-item>
  </el-form>
</template>

<script setup>
import { listRegion } from '@/api/region'
import { ref, reactive, watch } from 'vue'

//  props
const props = defineProps({
  address: {
    type: Object,
    default: null
  },
  size: {
    type: String,
    default: 'default'
  }
})

// 
const emit = defineEmits(['submit', 'cancel'])

// 
const form = reactive({
  id: null,
  consignee: '',
  sex: '',
  phone: '',
  region: [], //  [provinceCode, cityCode, districtCode]
  detail: '',
  isDefault: 0,
  //  name 
  provinceName: '',
  cityName: '',
  districtName: ''
})

// 
const resetForm = () => {
  form.id = null
  form.consignee = ''
  form.sex = ''
  form.phone = ''
  form.region = []
  form.detail = ''
  form.isDefault = 0
  form.provinceName = ''
  form.cityName = ''
  form.districtName = ''
}



// 
const rules = {
  consignee: [{ required: true, message: '', trigger: 'blur' }],
  sex: [{ required: false, message: '', trigger: 'change' }],
  phone: [
    { required: true, message: '', trigger: 'blur' },
    { pattern: /^1[3-9]\d{9}$/, message: '', trigger: 'blur' }
  ],
  region: [{ type: 'array', required: true, message: '', trigger: 'change' }],
  detail: [{ required: true, message: '', trigger: 'blur' }]
}

//  API  store 
const regionOptions = ref([])

// 
const formRef = ref()

//  address 
watch(() => props.address, (newVal) => {
  if (newVal) {
    form.id = newVal.id
    form.consignee = newVal.consignee
    form.sex = newVal.sex
    form.phone = newVal.phone
    form.detail = newVal.detail
    form.isDefault = newVal.isDefault || 0

    //  code 
    const codes = [newVal.provinceCode, newVal.cityCode, newVal.districtCode]
    form.region = codes.filter(Boolean)

    //  name 
    form.provinceName = newVal.provinceName || ''
    form.cityName = newVal.cityName || ''
    form.districtName = newVal.districtName || ''
  } else {
    resetForm()
  }
}, { immediate: true })

// 
const submit = async () => {
  try {
    await formRef.value.validate()
    // 
    const [provinceCode, cityCode, districtCode] = form.region
    const submitData = {
      id: form.id,
      userId: form.userId, // 
      consignee: form.consignee,
      sex: form.sex,
      phone: form.phone,
      provinceCode: provinceCode,
      provinceName: getRegionLabel(provinceCode),
      cityCode: cityCode,
      cityName: getRegionLabel(cityCode),
      districtCode: districtCode,
      districtName: getRegionLabel(districtCode),
      detail: form.detail,
      isDefault: form.isDefault,
      createTime: undefined, // 
      updateTime: undefined // 
    }
    emit('submit', submitData)
  } catch (error) {
    console.error('', error)
  }
}

// 
const cancel = async() => {
  emit('cancel')
  await resetForm()
}

// 
defineExpose({
  submit,
  cancel,
  resetForm
})

const getRegionLabel = (code) => {
  if (!code) return ''

  const findLabel = (options, targetCode) => {
    for (const item of options) {
      if (item.value === targetCode) return item.label
      if (item.children && item.children.length > 0) {
        const result = findLabel(item.children, targetCode)
        if (result) return result
      }
    }
    return ''
  }

  return findLabel(regionOptions.value, code)
}

// 
const loadRegionData = async () => {
  try {
    const response = await listRegion()
    const regions = response.data //  List<Region> 

   console.log("",regions)
    const options = convertToCascaderFormat(regions)
    regionOptions.value = options
     console.log("2",regionOptions.value)
  } catch (error) {
    console.error(':', error)
    // 
  }
}
function convertToCascaderFormat(data) {
  return data.map(item => ({
    value: item.code,
    label: item.name,
    children: item.children ? convertToCascaderFormat(item.children) : []
  }));
}

// 
loadRegionData()
</script>