import request from '@/utils/request'


// 
export function getCakes(params) {
  return request({
    url: '/cakes/list',
    method: 'get',
    params: params
  })
}

// 
export function addCake(data) {
  return request({
    url: '/cakes/add',
    method: 'post',
    data,
    headers:{
      'Content-Type': 'application/json;charset=utf-8'
    }
  })
}
//id
export function getCakeById(id){
  return request({
    url:`/cakes/get/${id}`,
    method:'get'
  })
}

//
export function updateCake(data){
  return request({
    url:'/cakes/update',
    method:'put',
    data: data,
    headers: {
      'Content-Type': 'application/json;charset=utf-8'
    }
  })
}

//
export function deleteCake(id){
  return request({
    url: `/cakes/delete/${id}`,
    method: 'delete'
  })
}

//
export function getTastes(){
  return request({
    url: '/cakes/taste/list',
    method: 'get'
  })
}

//
export function saveSpecs(productId,specs){
  return request({
    url: `/update/${productId}`,
    method: 'post',
    data: specs
  })
}