<template>
  <div class="simple-payment-container">
    <div class="payment-content">
      <!--  -->
      <div class="payment-header">
        <h2></h2>
        <p></p>
      </div>

      <!--  -->
      <el-card class="order-info-card">
        <div class="order-info">
          <div class="order-no">
            <el-icon><Document /></el-icon>
            {{ orderInfo.orderNo }}
          </div>
          <div class="order-amount">
            <span class="amount-label"></span>
            <span class="amount-value">{{ orderInfo.totalAmount }}</span>
          </div>
        </div>
      </el-card>

      <!--  -->
      <el-card class="payment-method-card">
        <h3></h3>
        <div class="payment-options">
          <div
            class="payment-option"
            :class="{ active: selectedPayment === 1 }"
            @click="selectedPayment = 1"
          >
            <img src="@/assets/images/wechat-pay.png" alt="" />
            <span></span>
          </div>
          <div
            class="payment-option"
            :class="{ active: selectedPayment === 2 }"
            @click="selectedPayment = 2"
          >
            <img src="@/assets/images/alipay.png" alt="" />
            <span></span>
          </div>
        </div>
      </el-card>

      <!--  -->
      <el-card class="password-card">
        <h3></h3>
        <div class="password-input-section">
          <div class="password-input-container">
            <!--   -->
            <el-input
              v-model="password"
              type="password"
              placeholder="6~20"
              minlength="6"
              maxlength="20"
              show-password
              class="password-input"
              :disabled="paying"
              @keyup.enter="confirmPayment"
            />
          </div>
          <p class="password-tip">6~20</p>
        </div>
      </el-card>

      <!--  -->
      <div class="payment-actions">
        <el-button 
          @click="goBack" 
          class="back-btn"
          :disabled="paying"
        >
          
        </el-button>
        <el-button 
          type="primary" 
          @click="confirmPayment" 
          class="pay-btn"
          :loading="paying"
          :disabled="!canPay"
        >
          {{ paying ? '...' : ` ${orderInfo.totalAmount}` }}
        </el-button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import { Document } from '@element-plus/icons-vue'
import { payOrder as apiPayOrder } from '@/api/order'

const route = useRoute()
const router = useRouter()

// 
const orderInfo = ref({
  id: '',
  orderNo: '',
  totalAmount: '0.00'
})

// 
const selectedPayment = ref(1)

// 
const password = ref('')
const paying = ref(false)

//  6~20
const canPay = computed(() => {
  return orderInfo.value.id && password.value.length >= 6 && password.value.length <= 20
})

// 
const loadOrderData = async () => {
  const { id, orderNo, amount } = route.query

  if (!id && !orderNo) {
    ElMessage.error('')
    router.back()
    return
  }

  orderInfo.value = {
    id: id || '',
    orderNo: orderNo || `ORDER${Date.now()}`,
    totalAmount: amount || '0.00',
  }
   console.log("",orderInfo.value)
}

//  
const confirmPayment = async () => {
  if (!orderInfo.value.id) {
    ElMessage.error('')
    return
  }

  if (password.value.length < 6 || password.value.length > 20) {
    ElMessage.error('6~20')
    return
  }

  paying.value = true
  try {
    const paymentData = {
      id: orderInfo.value.id,
      payMethod: selectedPayment.value,
      totalAmount: orderInfo.value.totalAmount,
      password: password.value
    }

    console.log(':', paymentData)
    const res = await apiPayOrder(paymentData)
    
    if (res.code === 200) {
      ElMessage.success('')
      setTimeout(() => {
        router.push('/order')
      }, 1500)
    } else {
      ElMessage.error(res.message || '')
      password.value = ''
    }
  } catch (error) {
    console.error(':', error)
    ElMessage.error('')
    password.value = ''
  } finally {
    paying.value = false
  }
}

const goBack = () => router.back()

onMounted(() => loadOrderData())
</script>

<style scoped>
.simple-payment-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #fdf6e3 0%, #faf4eb 100%);
  padding: 40px 20px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.payment-content {
  width: 100%;
  max-width: 500px;
}

.payment-header {
  text-align: center;
  margin-bottom: 30px;
}

.payment-header h2 {
  margin: 0;
  color: #b38b2d;
  font-size: 28px;
  font-weight: 600;
  background: linear-gradient(135deg, #d6bb70, #b38b2d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.payment-header p {
  margin: 8px 0 0;
  color: #8a7c5a;
  font-size: 14px;
}

.order-info-card,
.payment-method-card,
.password-card {
  margin-bottom: 20px;
  border-radius: 12px;
  border: 1px solid #e8dfc8;
  background: rgba(255, 253, 247, 0.9);
}

:deep(.el-card__body) {
  padding: 20px;
}

.order-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.order-no {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #5d5340;
  font-weight: 500;
}

.order-amount {
  text-align: right;
}

.amount-label {
  color: #8a7c5a;
  font-size: 14px;
}

.amount-value {
  color: #d6bb70;
  font-weight: bold;
  font-size: 24px;
}

.payment-method-card h3,
.password-card h3 {
  margin: 0 0 16px 0;
  color: #b38b2d;
  font-size: 16px;
  font-weight: 600;
}

.payment-options {
  display: flex;
  gap: 12px;
}

.payment-option {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 16px;
  border: 2px solid #f0e9d7;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  background: white;
}
.payment-option img {
  width: 28px;
  height: 28px;
  object-fit: contain;
  margin-right: 4px;
}


.payment-option:hover {
  border-color: #d6bb70;
}

.payment-option.active {
  border-color: #d6bb70;
  background: rgba(214, 187, 112, 0.05);
  box-shadow: 0 2px 8px rgba(214, 187, 112, 0.2);
}

.password-input-section {
  text-align: center;
}

.password-input-container {
  max-width: 300px;
  margin: 0 auto 12px;
}

:deep(.password-input .el-input__inner) {
  height: 50px;
  font-size: 18px;
  text-align: center;
  border: 2px solid #d6bb70;
  border-radius: 8px;
}

.password-tip {
  color: #8a7c5a;
  font-size: 14px;
  margin: 0;
}

.payment-actions {
  display: flex;
  gap: 12px;
  margin-top: 30px;
}

.back-btn,
.pay-btn {
  flex: 1;
  height: 48px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
}

.pay-btn {
  background: linear-gradient(135deg, #d6bb70, #c4a85f);
  border: none;
  color: white;
}

.pay-btn:hover:not(:disabled) {
  background: linear-gradient(135deg, #c4a85f, #b38b2d);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

.pay-btn:disabled {
  background: #cccccc;
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .payment-actions {
    flex-direction: column;
  }
}
</style>
