<!-- views/SearchView.vue -->
<template>
  <div class="search-page">
    <!--  -->
    <div class="search-header">
      <h1></h1>
      <p v-if="keyword" class="search-tip">
         "<strong>{{ keyword }}</strong>"  {{ results.length }} 
      </p>
    </div>

    <!--  -->
    <div class="cake-list" v-if="results.length > 0">
      <el-row :gutter="20">
        <el-col
          v-for="cake in results"
          :key="cake.id"
          :xs="24" :sm="12" :md="8" :lg="6"
        >
          <CakeCard :cake="cake" class="cake-card" @click="goToDetail(cake.id)">
            <template #actions>
              <el-button size="small" @click.stop="handleEdit(cake)" class="edit-btn"></el-button>
              <el-button size="small" @click.stop="handleDelete(cake.id)" class="delete-btn"></el-button>
            </template>
          </CakeCard>
        </el-col>
      </el-row>
      
    </div>

    <!--  -->
    <div v-else class="no-results" v-show="loaded">
      <el-empty
        description=""
        :image-size="100"
      >
        <el-button type="primary" @click="$router.push('/')" class="home-btn"></el-button>
      </el-empty>
    </div>

    <!--  -->
    <div v-if="loading" class="loading">
      <el-skeleton
        v-for="n in 6"
        :key="n"
        :rows="3"
        animated
      />
    </div>

    <!--  -->
    <el-dialog
      v-if="selectedCake"
      :title="` ${getCategoryName(selectedCake.categoryId)} `"
      v-model="showEditDialog"
      width="50%"
      @close="closeDialog"
      class="edit-dialog"
    >
      <component 
        :is="getFormComponent(selectedCake.categoryId)"
        :initial-data="selectedCake"
        ref="form"
        @submit="handleSubmit"
        @cancel="closeDialog"
      ></component>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, onMounted, watch,nextTick } from 'vue'
import { useRoute } from 'vue-router'
import CakeCard from '@/components/CakeCard.vue' // 
import CakeForm from '@/components/CakeForm.vue' // 
import DessertForm from '@/components/DessertForm.vue' // 
import PetCakeForm from '@/components/PetCakeForm.vue' // 
import { searchProducts,update } from '@/api/search' //  API
import { ElMessage } from 'element-plus'
import router from '@/router'

const route = useRoute()
const keyword = ref('')
const results = ref([])
const loading = ref(false)
const loaded = ref(false) // 
const selectedCake = ref(null) // 
const showEditDialog = ref(false) // 
const currentProduct = ref(null)
const form = ref(null)

// 
const getKeyword = () => {
  const q = route.query.q
  keyword.value = q ? decodeURIComponent(q) : ''
}

// 
const performSearch = async () => {
  if (!keyword.value.trim()) {
    results.value = []
    loaded.value = true
    return
  }

  loading.value = true
  try {
    const res = await searchProducts(keyword.value)
    //  { data: [...] }
    results.value = res.data || []
  } catch (error) {
    ElMessage.error('')
    results.value = []
  } finally {
    loading.value = false
    loaded.value = true
  }
}

// 
onMounted(() => {
  getKeyword()
  performSearch()
})

//  ?q=xxx
watch(
  () => route.query.q,
  (newQ) => {
    const decoded = newQ ? decodeURIComponent(newQ) : ''
    keyword.value = decoded
    performSearch()
  }
)

// 
watch(
  () => keyword.value,
  (kw) => {
    document.title = kw ? ` "${kw}" - ` : ' - '
  },
  { immediate: true }
)

// ID
const getFormComponent = (categoryId) => {
  switch (categoryId) {
    case 1:
      return CakeForm;
    case 2:
      return DessertForm;
    case 3:
      return PetCakeForm;
    default:
      return null;
  }
}

//
const goToDetail = (id) => {
  router.push(`/cake/${id}`)
}

// 
const getCategoryName = (categoryId) => {
  switch (categoryId) {
    case 1:
      return '';
    case 2:
      return '';
    case 3:
      return '';
    default:
      return '';
  }
}

// 
const handleEdit = async(product) => {

  selectedCake.value = {...product};
  showEditDialog.value = true;

   // 
  await nextTick(async () => {
   
      if (form.value && typeof form.value.setFormData === 'function') {
        
        form.value.setFormData(product)
      } else {
        console.warn('form  setFormData ')
      }
    })
  
}

// 
const handleSubmit = (data) => {
  // 
  console.log("sp",data)
  try{
    update(data);
    ElMessage.success("");
  }catch(error){
    ElMessage.error("")
  }
  closeDialog();
}

// 
const closeDialog = () => {
  showEditDialog.value = false;
  selectedCake.value = null;
}
</script>

<style scoped>
.search-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fefaf0;
}

.search-header {
  margin-bottom: 30px;
  text-align: center;
}

.search-header h1 {
  font-size: 28px;
  color: #d6bb70;
  margin-bottom: 10px;
  font-weight: 600;
}

.search-tip {
  color: #666;
  font-size: 14px;
}

.search-tip strong {
  color: #d6bb70;
}

.cake-list {
  min-height: 400px;
}

.cake-card {
  background-color: #fffdf7;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(214, 187, 112, 0.15);
  transition: all 0.3s ease;
  border: 1px solid #f5ecd8;
}

.cake-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(214, 187, 112, 0.25);
}

/*  */
.edit-btn {
  background: #f5ecd8;
  color: #b38b2d;
  border: none;
  border-radius: 15px;
  transition: all 0.3s;
}

.edit-btn:hover {
  background: #d6bb70;
  color: white;
}

/*  */
.delete-btn {
  background: #fdf6f0;
  color: #d6bb70;
  border: 1px solid #d6bb70;
  border-radius: 15px;
  transition: all 0.3s;
  margin-right: 8px;
}

.delete-btn:hover {
  background: #d6bb70;
  color: white;
}

.no-results {
  text-align: center;
  margin: 60px 0;
  color: #999;
}

.home-btn {
  background-color: #d6bb70;
  border: none;
  color: white;
  font-weight: 500;
  transition: all 0.3s;
}

.home-btn:hover {
  background-color: #c4a85f;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

.loading {
  margin-top: 20px;
}

/*  */
:deep(.edit-dialog .el-dialog) {
  border-radius: 12px;
  background-color: #fffdf7;
  border: 1px solid #f5ecd8;
}

:deep(.edit-dialog .el-dialog__header) {
  background-color: #fefaf0;
  border-bottom: 1px solid #f5ecd8;
  border-radius: 12px 12px 0 0;
}

:deep(.edit-dialog .el-dialog__title) {
  color: #d6bb70;
  font-weight: 600;
}

/*  */
@media (max-width: 768px) {
  .search-page {
    padding: 15px;
  }
  
  .search-header h1 {
    font-size: 24px;
  }
}
</style>